s las de la ley, su gorrita a varios colores.")]),_c('p',[_vm._v("Es que, con ese solo gesto, quedan automáticamente convertidos en otras personas, como si la gorrita no fuera sino el uniforme de una nueva personalidad. No sé si mi matrícula de hincha esté todavía demasiado fresca para permitirme ciertas observaciones personales acerca del partido de ayer, pero como ya hemos quedado de acuerdo en que una de las condiciones esenciales del hinchaje es la pérdida absoluta y aceptada del sentido del ridículo, voy a decir lo que vi --o lo que creí ver ayer tarde—para darme el lujo de empezar bien temprano a meter esas patas deportivas que bien guardadas me tenía. En primer término, me pareció que el Junior dominó a Millonarios desde el primer momento. Si la línea blanca que divide la cancha en dos mitades significa algo, mi afirmación anterior es cierta, puesto que muy pocas veces pudo estar la bola, en el primer tiempo, dentro de la mitad correspondiente a la portería del Junior. (¿Qué tal va mi debut como comentarista de fútbol?) …")])]),_c('div',{staticClass:"tarjeta bgr_9 p-5",attrs:{"titulo":"Ley (ámbito legislativo)"}},[_c('h3',[_vm._v("Código de infancia y adolescencia")]),_c('h4',[_vm._v("Principio y definiciones ")]),_c('p',[_vm._v("Artículo 1º. Finalidad. Este Código tiene por finalidad garantizar a los niños, a las niñas y a los adolescentes su pleno y armonioso desarrollo para que crezcan en el seno de la familia y de la comunidad, en un ambiente de felicidad, amor y comprensión. Prevalecerá el reconocimiento a la igualdad y la dignidad humana, sin discriminación alguna. Artículo 2º. Objeto. El presente Código tiene por objeto establecer normas sustantivas y procesales para la protección integral de los niños, las niñas y los adolescentes, garantizar el ejercicio de sus derechos y libertades consagrados en los instrumentos internacionales de derechos humanos, en la Constitución Política y en las leyes, así como su restablecimiento. Dicha garantía y protección